.header {
    background-color: #4167B2;
    display: grid;
    grid-template-columns: 1fr 10fr;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    /*display: flex;*/
    align-items: center;

}
.header img {
    width: 120px;
}
.header h1 {
    color: whitesmoke;
    text-align: center;
    font-family: "Arial Black";
    font-size: 48px;
   /*margin-left: 500px;*/
}
.homeLink a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: white;
    font-size: 24px;
    font-weight: bold;

}
    .homeLink a:hover {
        color: gold;
    }

